import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {ViewServiceComponent} from './view-service/view-service.component';
import {SharedModule} from '../../shared/shared.module';
import {RouterModule} from '@angular/router';
import {ServicesListComponent} from './services/services.component';
import {LoadingAnimationComponent} from '../../shared/loading-animation/loading-animation.component';
import {LottieModule} from 'ngx-lottie';
import {playerFactory} from '../pricing/pricing.module';
import {AddEditChildServiceComponent} from './add-edit-child-service/add-edit-child-service.component';
import {TranslateModule} from '@ngx-translate/core';
import {AddParentServiceComponent} from './add-parent-service/add-parent-service.component';
import {AddServiceComponent} from '../pricing/decidesteps/add-step-cat-serv/add-service/add-service.component';
import { AddFeatureComponent } from './view-service/add-feature/add-feature.component';

const routes = [
  {

    path: 'viewService/:id',
    component: ViewServiceComponent
  }, {

    path: 'add-child/:parentId',
    component: AddEditChildServiceComponent
  }, {

    path: 'add-parent/:parentId',
    component: AddParentServiceComponent
  }, {

    path: 'list',
    component: ServicesListComponent
  }, {

    path: '',
    redirectTo: 'list'
  },
];


@NgModule({
  declarations: [ViewServiceComponent, ServicesListComponent, LoadingAnimationComponent, AddEditChildServiceComponent, AddParentServiceComponent, AddFeatureComponent],
  imports: [
    CommonModule, SharedModule, RouterModule.forChild(routes),
    LottieModule.forRoot({player: playerFactory}), TranslateModule
  ]
})
export class ServicesModule {
}
