import {Component, OnInit} from '@angular/core';
import {AddPartnerComponent} from '../../../home/home/partners/add-partner/add-partner.component';
import {ActivatedRoute, Router} from '@angular/router';
import {Apollo} from 'apollo-angular';
import {AuthService} from '../../../../services/auth.service';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {oneFeature, onePartner} from '../../../home/home/homequeries';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {createPartner, createServiceFeature, updateProduct, updateServiceFeature} from '../../../home/home/homemutation';

@Component({
  selector: 'app-add-feature',
  templateUrl: './add-feature.component.html',
  styleUrls: ['./add-feature.component.scss']
})
export class AddFeatureComponent implements OnInit {
  serviceId: any;
  id: number;
  featuresForm: FormGroup;
  feature: any;
  loading = true;
  icon: any;
  image: any;
  toSend: any;
  toUpdate: any;
  lang = 'en';

  constructor(
    public dialogRef: MatDialogRef<AddFeatureComponent>,
    private apollo: Apollo, private fb: FormBuilder, private router: Router) {
    this.lang = localStorage.getItem('lang') || 'en';
  }

  ngOnInit(): void {
    this.createNullForm();
    if ((this.id != null) && (this.id !== undefined)) {
      this.get();
    } else {
      this.createNullForm();
    }
  }

  get(): void {
    this.apollo.watchQuery({
      query: oneFeature,
      fetchPolicy: 'network-only',
      variables: {id: Number(this.id)}
    }).valueChanges.subscribe((result: any) => {
      this.loading = false;
      this.feature = result?.data?.findOneServiceFeature;
      this.createExistingForm();
    });
  }

  createExistingForm(): void {
    this.featuresForm = this.fb.group({
      id: Number(this.id),
      title: this.fb.group({
        [this.lang]: [this.feature.title, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
      desc: this.fb.group({
        [this.lang]: [this.feature.desc, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
    });
  }

  createNullForm(): void {
    this.featuresForm = this.fb.group({
      title: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
      desc: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
      service: [this.serviceId, Validators.required]
    });
  }

  send(): void {

    this.apollo.mutate({
      mutation: createServiceFeature,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe(response => {
      if (response.data) {
        alert('Feature created successfully');
        this.close();
      }
    }, error => alert(error));
  }

  close() {
    this.dialogRef.close();

  }


  update(): void {


  }

  submit(): void {
    this.toSend = this.featuresForm.value;
    this.toUpdate = this.featuresForm.value;
    this.toSend.image = this.image;
    this.id ? this.update() : this.send();
  }

  handleFileInput(type, e): void {
    this.image = e.target.files[0];

  }

}
