import {Component, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Apollo} from 'apollo-angular';
import {ActivatedRoute, Router} from '@angular/router';
import {findOneChildServiceQuery, findOneParentServiceQuery} from '../../home/home/homequeries';
import {
  createChildService,
  createProduct,
  updateProduct,
  updateProductIcon,
  updateProductLogo,
  updateService
} from '../../home/home/homemutation';

@Component({
  selector: 'app-add-edit-child-service',
  templateUrl: './add-edit-child-service.component.html',
  styleUrls: ['./add-edit-child-service.component.scss']
})
export class AddEditChildServiceComponent implements OnInit {
  serviceForm: FormGroup;
  fileToUpload: any;
  toSend: any;
  toUpdate: any;
  lang = 'en';
  parentId: any;
  service: any;
  id: any;
  icon: any;
  iconUrl: any;
  logo: any;
  logoUrl: any;
  changeLogo = false;

  constructor(private fb: FormBuilder, private apollo: Apollo, private route: ActivatedRoute, private router: Router) {
    this.lang = localStorage.getItem('lang') || 'en';
  }

  updateIcon(): void {
    this.apollo.mutate({
      mutation: updateProductIcon,
      variables: {id: this.toUpdate.id, icon: this.icon},
      context: {useMultipart: true}
    }).subscribe(response => {
    }, error => alert('Icon Not Updated'));
  }

  updateLogo(): void {
    this.apollo.mutate({
      mutation: updateProductLogo,
      variables: {id: this.toUpdate.id, logo: this.logo},
      context: {useMultipart: true}
    }).subscribe(response => {
    }, error => alert('logo not updated'));
  }

  readURL(event: any, localVar): void {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = e => this[localVar] = reader.result;
    }
  }

  handleFileInput(type, e): void {
    if (type === 'icon') {
      if (e.target.files[0]) {
        this.icon = e.target.files[0];
        this.readURL(e, 'iconUrl');
      } else {
        this.icon = null;
        this.iconUrl = null;

      }

    } else if (type === 'logo') {
      if (e.target.files[0]) {
        this.logo = e.target.files[0];
        this.readURL(e, 'logoUrl');
        this.logoUrl = URL.createObjectURL(this.logo);
      } else {
        this.logo = null;
        this.logoUrl = null;
      }
    }
  }

  isEditing(): boolean {
    return (this.id && (this.id !== null) && (this.id !== 'null') && (this.id !== undefined));
  }

  ngOnInit(): void {
    this.createNullForm();
    this.route.params.subscribe(params => {
      this.parentId = params.parentId;
    });
    this.route.queryParams.subscribe(params => {
      this.id = params.childId;
      if (this.isEditing()) {
        this.get();
      } else {
        this.createNullForm();
      }
    });
  }

  get(): void {
    this.apollo.query({
      query: findOneChildServiceQuery,
      fetchPolicy: 'network-only',
      variables: {id: Number(this.id)}
    }).subscribe((result: any) => {
      this.service = result?.data?.findOneChildService;
      this.createExistingForm();
    });
  }
  createExistingForm(): void {

    this.serviceForm = this.fb.group({
      parentService: this.parentId,
      id: Number(this.service.id),
      onHome: [this.service.onHome],
      isActive: [this.service.isActive],
      title: this.fb.group({
        [this.lang]: [this.service.title, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
      fullDesc: this.fb.array(this.service.fullDesc)

    });
  }

  createNullForm(): void {
    this.serviceForm = this.fb.group({
      parentService: [Number(this.parentId)],
      onHome: [''],
      isActive: [''],
      title: this.fb.group({
        ar: [''],
        en: ['']
      }),
      fullDesc: this.fb.array([])
    });
  }

  send(): void {
    this.apollo.mutate({
      mutation: createChildService,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe(response => {
      if (response.data) {
        // alert('Child Service created successfully');
        this.router.navigateByUrl('/services/list');
      }
    }, error => alert(error));
  }

  update(): void {

  }

  submit(): void {
    this.toSend = this.serviceForm.value;
    this.toUpdate = this.serviceForm.value;
    this.toSend.icon = this.icon;
    this.toSend.logo = this.logo;
    this.isEditing() ? this.update() : this.send();
  }

  localizedObj(): FormGroup {
    return this.fb.group({
      ar: ['', Validators.required],
      en: ['', Validators.required]
    });
  }

  get desc(): FormArray {
    return this.serviceForm.get('fullDesc') as FormArray;
  }

  addDesc(): void {
    this.desc.push(this.localizedObj());
  }


  removeDesc(i: number): void {
    this.desc.removeAt(i);
  }
}
