import {Component, OnInit} from '@angular/core';
import {OwlOptions} from 'ngx-owl-carousel-o';
import {Apollo} from 'apollo-angular';
import {parentServiceQuery, removeParentService, removeProduct} from '../../home/home/homequeries';
import {AuthService} from '../../../services/auth.service';
import {AddServiceComponent} from '../../pricing/decidesteps/add-step-cat-serv/add-service/add-service.component';
import {MatDialog} from '@angular/material/dialog';
import {AddParentServiceComponent} from '../add-parent-service/add-parent-service.component';
import {Router} from '@angular/router';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesListComponent implements OnInit {
  services: any[] = [];
  admin = false;
  loading = true;

  constructor(private apollo: Apollo, private auth: AuthService, public dialog: MatDialog, private router: Router) {
  }

  ngOnInit(): void {
    this.auth.checkAdmin().subscribe(x => this.admin = x);
    this.loading = true;
    this.getParentServices();
  }

  getParentServices(): void {
    this.apollo
      .watchQuery({
        query: parentServiceQuery, fetchPolicy: 'network-only'
      })
      .valueChanges.subscribe((result: any) => {
      this.services = result?.data?.findManyParentServices.entities;
      this.loading = false;
    });
  }

  openDialogService(): void {
    const dialogRef1 = this.dialog.open(AddParentServiceComponent);
    // dialogRef1.componentInstance.id = id;
    dialogRef1.afterClosed().subscribe(result => {
      this.getParentServices();
    });

  }

  deleteService(serv): void {
    if (serv.childServices.length) {
      alert('please delete all children first');
    } else {
      const conf = confirm('are you sure you want to delete this service');
      if (conf) {
        alert('deleting');
        this.loading = true;
        this.apollo.mutate({
          mutation: removeParentService,
          variables: {id: Number(serv.id)}
        }).subscribe((result: any) => {
          this.loading = false;
          alert('deleted successfully');
          this.router.navigateByUrl('/services/list');
        });

      }
    }
  }

}
