import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {OwlOptions} from 'ngx-owl-carousel-o';
import {
  findOneServiceQuery,
  findServiceWithSameParent,
  homeQuery,
  removeChildService,
  removeParentService, removeProduct, removeServiceFeature
} from '../../home/home/homequeries';
import {Apollo} from 'apollo-angular';
import {AuthService} from '../../../services/auth.service';
import {AddPartnerComponent} from '../../home/home/partners/add-partner/add-partner.component';
import {MatDialog} from '@angular/material/dialog';
import {AddFeatureComponent} from './add-feature/add-feature.component';

@Component({
  selector: 'app-view-service',
  templateUrl: './view-service.component.html',
  styleUrls: ['./view-service.component.scss']
})
export class ViewServiceComponent implements OnInit {
  admin = false;
  id: any;
  parentId: any;
  loading = true;
  service: any;
  services: any;
  customOptions: OwlOptions = {
    autoWidth: true,
    margin: 10,
    loop: false,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    dots: true,
    nav: true,

    navSpeed: 700,
    navText: ['<', '>'],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 2
      },
      940: {
        items: 3
      },
      1024: {
        items: 3
      }
    },
  };

  constructor(private route: ActivatedRoute,
              private apollo: Apollo,
              private auth: AuthService,
              private router: Router,
              public dialog: MatDialog) {

  }


  deleteFeature(id) {
    const confirmme = confirm('are you sure you want to delete');
    if (confirmme) {
      this.loading = true;
      this.apollo.mutate({
        mutation: removeServiceFeature,
        variables: {id: Number(id)}
      }).subscribe((result: any) => {
        this.loading = false;
        alert('deleted successfully');
        window.location.reload();
      });
    }
  }

  openDialog(serviceId, featureId): void {
    const dialogRef1 = this.dialog.open(AddFeatureComponent);
    dialogRef1.componentInstance.serviceId = serviceId;
    dialogRef1.componentInstance.id = featureId;

    dialogRef1.afterClosed().subscribe(result => {
      this.get(this.id, this.parentId);
    });

  }

  ngOnInit(): void {
    this.auth.checkAdmin().subscribe(x => this.admin = x);
    this.route.params.subscribe(params => {
      this.id = params.id;
      this.loading = true;
      this.route.queryParams.subscribe(param => {
        this.parentId = param.parent;
        this.get(this.id, this.parentId);
      });
    });


  }

  deleteService(serv): void {
    const confirmme = confirm('are you sure you want to delete this service');
    if (confirmme) {
      alert('deleting');
      this.loading = true;
      this.apollo.mutate({
        mutation: removeChildService,
        variables: {id: Number(serv.id)}
      }).subscribe((result: any) => {
        this.loading = false;
        alert('deleted successfully');
        this.router.navigateByUrl('/services/list');
      });

    }
  }

  private get(id, parent): void {
    this.loading = true;
    this.apollo
      .watchQuery({
          query: findOneServiceQuery,
          variables: {id: Number(id), parentId: Number(this.parentId)},
          fetchPolicy: 'network-only'
        }
      )
      .valueChanges.subscribe((result: any) => {
      this.loading = false;
      this.loading = result?.loading;
      this.service = result?.data.findOneChildService;
      this.services = result?.data.findOneParentService?.childServices;
      this.services = this.services.filter((item) => {
        return (Number(item.id) !== Number(this.id));
      });

    });
  }


}
