import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Apollo} from 'apollo-angular';
import {
  createParentService,
  sendjobRequest, updateParentIcon,
  updateParentService,
  updateProduct,
  updateProductIcon,
  updateProductLogo
} from '../../home/home/homemutation';
import {ActivatedRoute, Router} from '@angular/router';
import {findOneParentServiceQuery, oneProdQuery} from '../../home/home/homequeries';

@Component({
  selector: 'app-add-parent-service',
  templateUrl: './add-parent-service.component.html',
  styleUrls: ['./add-parent-service.component.scss']
})
export class AddParentServiceComponent implements OnInit {
  serviceForm: FormGroup;
  fileToUpload: any;
  imageUrl: any;
  toSend: any;
  toUpdate: any;
  lang = 'en';
  id: any;
  service: any;

  constructor(private fb: FormBuilder, private apollo: Apollo, private route: ActivatedRoute, private router: Router) {
    this.lang = localStorage.getItem('lang') || 'en';
  }

  updateIcon(): void {
    this.apollo.mutate({
      mutation: updateParentIcon,
      variables: {id: this.toUpdate.id, icon: this.fileToUpload},
      context: {useMultipart: true}
    }).subscribe(response => {
    }, error => alert('Icon Not Updated'));
  }

  ngOnInit(): void {
    this.createNullForm();
    this.route.params.subscribe(params => {
      this.id = params.parentId;
      if (this.isEditing()) {
        this.get();
      } else {
        this.createNullForm();
      }
    });
  }

  get(): void {
    this.apollo.query({
      query: findOneParentServiceQuery,
      fetchPolicy: 'network-only',
      variables: {id: Number(this.id)}
    }).subscribe((result: any) => {
      this.service = result?.data?.findOneParentService;
      this.createExistingForm();
    });
  }

  createExistingForm(): void {
    this.serviceForm = this.fb.group({
      id: Number(this.id),
      title: this.fb.group({
        [this.lang]: [this.service.title, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
      desc: this.fb.group({
        [this.lang]: [this.service.desc, Validators.required],
        // [(this.lang === 'ar') ? 'en' : 'ar']: ['']
      }),
    });
  }

  isEditing(): boolean {
    return (this.id && (this.id !== null) && (this.id !== 'null') && (this.id !== undefined));
  }

  updateNoImage(): void {
    this.apollo.mutate({
      mutation: createParentService,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe((response) => {
      alert('service created');
    }, error => alert(error));
  }

  updateWithImage(): void {
  }

  send(): void {

    this.apollo.mutate({
      mutation: createParentService,
      variables: this.toSend,
      context: {
        useMultipart: true
      }
    }).subscribe((response) => {
      alert('service created');
    }, error => alert(error));
  }

  submit(): void {
    this.toSend = this.serviceForm.value;
    this.toUpdate = this.serviceForm.value;
    this.toSend.icon = this.fileToUpload;
    this.isEditing() ? this.update() : this.send();
  }

  update(): void {

  }

  handleFileInput(e): void {
    if (e.target.files[0]) {
      this.fileToUpload = e.target.files[0];
      this.readURL(e, 'imageUrl');
    } else {
      this.fileToUpload = null;
      this.imageUrl = null;
    }
  }
  readURL(event: any, localVar): void {
    if (event.target.files && event.target.files[0]) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = e => this[localVar] = reader.result;
    }
  }

  createNullForm(): void {
    this.serviceForm = this.fb.group({
      title: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
      desc: this.fb.group({
        ar: ['', Validators.required],
        en: ['', Validators.required]
      }),
    });
  }
}
